import React, { useContext, useCallback, useImperativeHandle } from "react";
import AppContext from "../../context/AppContext";
import { Button, SxProps, Theme } from "@mui/material";

const WordBtn = React.forwardRef<any, { word: string }>(({ word }, ref) => {
  const { db } = useContext(AppContext);
  const play = useCallback(async () => {
    const str = word.split("");
    for (let i = 0; i < str.length; ++i) {
      await new Promise((resolve, reject) => {
        const audio = new Audio();
        audio.autoplay = true;
        audio.onended = resolve;
        audio.onerror = resolve;
        audio.onplay = () => {
          if (!isNaN(audio.currentTime)) audio.playbackRate = 3;
        };
        audio.src = getUrl(db[str[i]]?.[0].join(""));
      });
    }
  }, [db, word]);

  useImperativeHandle(ref, () => ({
    play,
  }));

  return (
    <Button sx={wordSx} disableRipple variant="text" onClick={() => play()}>
      {word}
    </Button>
  );
});

export default WordBtn;

const getUrl = (pronunciation: string) => {
  try {
    return `https://data.yuedict.app/wav/${pronunciation}.mp3`;
  } catch {
    return "";
  }
};

const wordSx: SxProps<Theme> = {
  cursor: "default",
  m: 0.5,
  minWidth: "max-content",
  fontSize: 18,
};
