import React, { useContext, useState } from "react";
import { Box, Button, Popover, Switch, SxProps, Theme } from "@mui/material";
import {
  Home as HomeIcon,
  Settings as SettingsIcon,
} from "@mui/icons-material";
import AppContext from "../context/AppContext";

const FtnRow = () => {
  const [anchorEl, setAnchorEl] = useState<HTMLButtonElement | null>(null);
  const { mode, setMode } = useContext(AppContext);

  return (
    <Box sx={rootSx}>
      <Box sx={breadcrumbSx}>
        <Button size="small" disableRipple>
          <HomeIcon />
        </Button>
      </Box>
      <Button
        size="small"
        disableRipple
        onClick={(e) => setAnchorEl(e.currentTarget)}
      >
        <SettingsIcon />
      </Button>
      <Popover
        open={Boolean(anchorEl)}
        anchorEl={anchorEl}
        onClose={() => setAnchorEl(null)}
        slotProps={{
          paper: {
            sx: menuSx,
          },
        }}
      >
        <Box>
          修改此頁
          <Switch
            checked={mode === "edit"}
            onChange={({ target: { checked } }) => {
              setMode(checked ? "edit" : "view");
            }}
          />
        </Box>
      </Popover>
    </Box>
  );
};

export default FtnRow;

const rootSx: SxProps<Theme> = {
  display: "flex",
  justifyContent: "space-between",
  p: 2,
  background: (t) => t.palette.background.default,
  "& button": {
    fontSize: "0.8em",
  },
};

const breadcrumbSx: SxProps<Theme> = {
  display: "flex",
};

const menuSx: SxProps<Theme> = {
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  "& > div": {
    display: "flex",
    width: "20ch",
    justifyContent: "space-between",
    alignItems: "center",
    px: 1,
  },
  py: 1,
};
