import React, { useContext, useRef } from "react";
import AppContext from "../context/AppContext";
import { Box, Button, SxProps, Theme } from "@mui/material";
import {
  Backspace as BackspaceIcon,
  PlayCircle as PlayCircleIcon,
  SmartScreen as SmartScreenIcon,
} from "@mui/icons-material";
import WordBtn from "../components/sentence/WordBtn";
import { useNavigate } from "react-router-dom";

const Sentence = () => {
  const { words, backspaceWord } = useContext(AppContext);
  const navigate = useNavigate();
  const wordRefs = useRef<any>([]);

  const play = async () => {
    for (let i = 0; i < wordRefs.current.length; ++i) {
      await wordRefs.current[i]?.play();
    }
  };

  return (
    <Box sx={rootSx}>
      <Box sx={wordContainerSx}>
        {words.map((word, idx) => (
          <WordBtn
            key={idx}
            word={word}
            ref={(el) => (wordRefs.current[idx] = el)}
          />
        ))}
      </Box>
      <Button onClick={backspaceWord} disableRipple>
        <BackspaceIcon />
      </Button>
      <Button onClick={() => play()} disableRipple>
        <PlayCircleIcon />
      </Button>
      <Button onClick={() => navigate(`/marquee`)} disableRipple>
        <SmartScreenIcon />
      </Button>
    </Box>
  );
};

export default Sentence;

const rootSx: SxProps<Theme> = {
  display: "flex",
  justifyContent: "space-between",
  px: 2,
};

const wordContainerSx: SxProps<Theme> = {
  display: "flex",
  border: "1px solid grey",
  borderRadius: "5px",
  overflow: "scroll",
  flex: 1,
  m: 1,
  minHeight: 53.5,
};
