import React, { useContext } from "react";
import AppContext from "../context/AppContext";
import { Box, SxProps, Theme } from "@mui/material";
import Item from "../components/Item";

const ItemGrids = () => {
  const { itemDb, curPage } = useContext(AppContext);

  const {
    dimension: { height, width },
    items,
  } = curPage;

  return (
    <Box sx={rootSx}>
      {Array(height)
        .fill(0)
        .map((v, rowIdx) => (
          <Box sx={rowSx} key={`row-${rowIdx}`}>
            {Array(width)
              .fill(0)
              .map((_v, colIdx) => (
                <Item
                  key={`cell-${items[rowIdx * width + colIdx]}`}
                  itemIdx={rowIdx * width + colIdx}
                  item={itemDb[curPage.items[rowIdx * width + colIdx]]}
                />
              ))}
          </Box>
        ))}
    </Box>
  );
};

export default ItemGrids;

const rootSx: SxProps<Theme> = {
  display: "flex",
  flexDirection: "column",
  flex: 1,
};

const rowSx: SxProps<Theme> = {
  display: "flex",
  flex: 1,
};
