import React, { useCallback, useEffect, useMemo, useState } from "react";
import { Item, Mode, Page } from "../types";
import { v4 as uuidv4 } from "uuid";
import DEFAULT_PAGE from "../defaultPage.json";
import DEFAULT_ITEMS from "../defaultItems.json";

const APP_PREFIX = "closer";

interface AppContextState {
  words: string[];
  db: any;
  itemDb: { [id: string]: Item };
  pages: { [name: string]: Page };
  pageStack: string[];
  curPage: Page;
  _curPage: Page;

  // edit mode
  mode: Mode;
  curDragId: string | null;

  // item editor
  itemId: string | null;
}

interface AppContextValue extends AppContextState {
  addWord: (word: string) => void;
  removeWord: (idx: number) => void;
  backspaceWord: () => void;
  clearWords: () => void;

  // edit mode
  isDirty: boolean;
  setMode: (mode: Mode) => void;
  handleDragStart: (id: string) => void;
  handleDragEnd: () => void;
  handleDragOver: (id: string) => void;
  emptyItemFromPage: (idx: number) => void;

  // item editor
  openItemEditor: (id: string | null) => void;
  saveItem: (item: Item) => void;
}

const AppContext = React.createContext<AppContextValue>({} as AppContextValue);

export const AppContextProvider = ({
  children,
}: {
  children: React.ReactElement;
}) => {
  const [state, setState] = useState<AppContextState>(DEFAULT_STATE);

  const addWord = useCallback(
    (word: string) =>
      setState((prev) => ({
        ...prev,
        words: [...prev.words, word],
      })),
    [setState]
  );

  const removeWord = useCallback(
    (idx: number) =>
      setState((prev) => ({
        ...prev,
        words: prev.words.filter((v, _idx) => idx !== _idx),
      })),
    [setState]
  );

  const backspaceWord = useCallback(
    () =>
      setState((prev) => ({
        ...prev,
        words: prev.words.filter((v, idx, self) => idx !== self.length - 1),
      })),
    [setState]
  );

  const clearWords = useCallback(
    () =>
      setState((prev) => ({
        ...prev,
        words: [],
      })),
    [setState]
  );

  const isDirty = useMemo(
    () => JSON.stringify(state._curPage) === JSON.stringify(state.curPage),
    [state._curPage, state.curPage]
  );

  const setMode = useCallback(
    (mode: Mode) => {
      setState((prev) => ({ ...prev, mode }));
    },
    [setState]
  );

  const handleDragStart = useCallback(
    (id: string) => {
      setState((prev) => ({ ...prev, curDragId: id }));
    },
    [setState]
  );

  const handleDragEnd = useCallback(
    () =>
      setState((prev) => ({
        ...prev,
        curDragId: null,
      })),
    [setState]
  );

  const handleDragOver = useCallback(
    (id: string) => {
      if (state.curDragId === id) return;
      setState((prev) => {
        const curPage = structuredClone(prev.curPage);
        let aIdx = curPage.items.findIndex((item) => item === prev.curDragId);
        let bIdx = curPage.items.findIndex((item) => item === id);
        const tmp = curPage.items[aIdx];
        curPage.items[aIdx] = curPage.items[bIdx];
        curPage.items[bIdx] = tmp;
        return {
          ...prev,
          curPage,
        };
      });
    },
    [state.curDragId, setState]
  );

  const emptyItemFromPage = useCallback(
    (idx: number) =>
      setState((prev) => {
        const curPage = structuredClone(prev.curPage);
        curPage.items[idx] = "";

        return {
          ...prev,
          curPage,
        };
      }),
    []
  );

  const openItemEditor = useCallback(
    (itemId: string | null) =>
      setState((prev) => ({
        ...prev,
        itemId,
      })),
    []
  );

  const saveItem = useCallback(
    (item: Item) =>
      setState((prev) => {
        const itemDb = structuredClone(prev.itemDb);
        if (item.id === "" || !Boolean(itemDb[item.id])) {
          item.id = uuidv4();
        }
        itemDb[item.id] = item;
        localStorage.setItem(`${APP_PREFIX}-itemDb`, JSON.stringify(itemDb));
        return {
          ...prev,
          itemId: null,
          itemDb,
        };
      }),
    []
  );

  useEffect(() => {
    if (localStorage.getItem("characters") === null) {
      fetch("https://data.yuedict.app/dict.json")
        .then((r) => r.json())
        .then((db) => {
          const characters = Object.entries(db).reduce((acc, [c, v]) => {
            // @ts-ignore
            acc[c] = v[6][0].map((_v) => _v[0]);
            return acc;
          }, {} as { [char: string]: string[][] });
          localStorage.setItem("characters", JSON.stringify(characters));
          setState((prev) => ({
            ...prev,
            db: characters,
          }));
        });
    }
  }, [setState]);

  return (
    <AppContext.Provider
      value={{
        ...state,
        addWord,
        removeWord,
        backspaceWord,
        clearWords,
        isDirty,
        setMode,
        handleDragStart,
        handleDragEnd,
        handleDragOver,
        emptyItemFromPage,
        openItemEditor,
        saveItem,
      }}
    >
      {children}
    </AppContext.Provider>
  );
};

export default AppContext;

const DEFAULT_STATE: AppContextState = {
  words: ["你好", "嗎"],
  db: JSON.parse(localStorage.getItem("characters") ?? "{}"),
  itemDb: {
    ...JSON.parse(
      localStorage.getItem(`${APP_PREFIX}-itemDb`) ??
        JSON.stringify(DEFAULT_ITEMS)
    ),
    "": {
      id: "",
      text: "",
    },
  },
  pages: {
    "": structuredClone(DEFAULT_PAGE),
  },
  pageStack: [""],
  mode: "edit",
  curDragId: null,
  curPage: structuredClone(DEFAULT_PAGE),
  _curPage: structuredClone(DEFAULT_PAGE),
  itemId: null,
};
