import React, { useContext, useEffect, useMemo, useState } from "react";
import {
  Box,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  InputLabel,
  SxProps,
  Switch,
  TextField,
  Theme,
  Button,
} from "@mui/material";
import AppContext from "../../context/AppContext";
import { Item } from "../../types";
import DemoItem from "../Item";
import ColorPicker from "./ColorPicker";
import { Save as SaveIcon, Close as CloseIcon } from "@mui/icons-material";

const ItemDialog = () => {
  const { itemId, itemDb, openItemEditor, saveItem } = useContext(AppContext);

  const [item, setItem] = useState<Item>(DEFAULT_ITEM);

  const title = useMemo(
    () => (item.id === "" ? "加入新詞語" : `修改詞語：${itemDb[item.id].text}`),
    [item, itemDb]
  );

  const handleChange = (field: keyof Item, v: any) => {
    setItem((prev) => ({ ...prev, [field]: v }));
  };

  useEffect(() => {
    if (itemId && itemDb[itemId]) {
      setItem(itemDb[itemId]);
    } else {
      setItem(DEFAULT_ITEM);
    }
  }, [itemId, itemDb]);

  return (
    <Dialog open={Boolean(itemId)} fullWidth>
      <DialogTitle>{title}</DialogTitle>
      <DialogContent>
        <Box sx={contentSx}>
          <Box sx={demoContainerSx}>
            <DemoItem item={item} demo />
          </Box>
          <Box>
            <InputLabel>詞語</InputLabel>
            <TextField
              label="詞語"
              value={item.text}
              onChange={({ target: { value } }) => handleChange("text", value)}
              size="small"
            />
          </Box>
          <Box>
            <InputLabel>圖片</InputLabel>
            <TextField
              label="圖片網址"
              value={item.img ?? ""}
              onChange={({ target: { value } }) => handleChange("img", value)}
              size="small"
            />
          </Box>
          <Box>
            <InputLabel>背景顏色</InputLabel>
            <ColorPicker
              value={item.backgroundColor ?? null}
              defaultValue="white"
              onChange={(e) => handleChange("backgroundColor", e ?? "white")}
            />
          </Box>
          <Box>
            <InputLabel>跳到頁面</InputLabel>
            <TextField
              label="跳到頁面"
              value={item.pageLink ?? ""}
              onChange={({ target: { value } }) =>
                handleChange("pageLink", value)
              }
              size="small"
            />
          </Box>
          <Box
            sx={{ visibility: Boolean(item.pageLink) ? "visible" : "hidden" }}
          >
            <InputLabel>點擊時加到句子？</InputLabel>
            <Switch
              checked={item.typeIn ?? true}
              onChange={({ target: { checked } }) =>
                handleChange("typeIn", checked)
              }
              size="small"
            />
          </Box>
        </Box>
      </DialogContent>
      <DialogActions sx={actionContainerSx}>
        <Button
          startIcon={<SaveIcon />}
          variant="contained"
          onClick={() => saveItem(item)}
        >
          儲存
        </Button>
        <Button
          startIcon={<CloseIcon />}
          variant="outlined"
          color="error"
          onClick={() => openItemEditor(null)}
        >
          取消
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default ItemDialog;

const DEFAULT_ITEM: Item = {
  id: "",
  text: "",
};

const contentSx: SxProps<Theme> = {
  py: 1,
  display: "flex",
  flexDirection: "column",
  gap: 1,
  alignItems: "center",

  "& > div": {
    display: "flex",
    justifyContent: "space-between",
    width: "100%",
  },
};

const demoContainerSx: SxProps<Theme> = {
  width: "100px !important",
  height: 100,
};

const actionContainerSx: SxProps<Theme> = {
  justifyContent: "flex-start",
};
