import React, { useContext } from "react";
import { Box, SxProps, Theme } from "@mui/material";
import AppContext from "../context/AppContext";
import { Item as ItemType } from "../types";
import {
  Add as AddIcon,
  DeleteOutlined as DeleteIcon,
  ControlCamera as ControlCameraIcon,
  Edit as EditIcon,
} from "@mui/icons-material";

interface WordGridProps {
  item: ItemType;
  itemIdx?: number;
  demo?: boolean;
}

const TMP_DRAGGING_ITEM_ID = "tmp-dragging-item";

const Item = React.memo(
  ({ item, itemIdx = -1, demo = false }: WordGridProps) => {
    const {
      addWord,
      mode,
      handleDragStart,
      handleDragOver,
      handleDragEnd,
      emptyItemFromPage,
      openItemEditor,
    } = useContext(AppContext);

    const handleClick = () => {
      if (demo) return;
      if (mode === "view") {
        if (item.text && (!item.pageLink || item.typeIn)) {
          addWord(item.text);
        }
      }
    };

    const handleDelete = () => {
      if (itemIdx !== -1) emptyItemFromPage(itemIdx);
    };

    return (
      <Box
        sx={{
          ...rootSx,
          background: item.backgroundColor ?? "white",
        }}
        onClick={handleClick}
        draggable={mode === "edit"}
        onDragStart={(e) => {
          handleDragStart(item.id);
          const crt = e.currentTarget.cloneNode(true) as HTMLElement;
          crt.id = TMP_DRAGGING_ITEM_ID;
          console.log(e.currentTarget.getBoundingClientRect().width);
          crt.style.height = `${
            e.currentTarget.getBoundingClientRect().height
          }px`;
          crt.style.width = `${
            e.currentTarget.getBoundingClientRect().width
          }px`;
          document.body.appendChild(crt);
          e.dataTransfer.setDragImage(
            crt,
            e.clientX - e.currentTarget.getBoundingClientRect().x,
            e.clientY - e.currentTarget.getBoundingClientRect().y
          );
        }}
        onDragEnd={() => {
          document.getElementById(TMP_DRAGGING_ITEM_ID)?.remove();
          handleDragEnd();
        }}
        onDragOver={(e) => {
          e.preventDefault();
          handleDragOver(item.id);
        }}
      >
        {item.text}
        {!demo && mode === "edit" && (
          <ControlCameraIcon fontSize="small" sx={moveIconSx} />
        )}
        {!demo && mode === "edit" && item.id && (
          <>
            <EditIcon
              fontSize="small"
              sx={editIconSx}
              onClick={() => {
                openItemEditor(item.id);
              }}
            />
            <DeleteIcon
              fontSize="small"
              sx={deleteIconSx}
              color="error"
              onClick={() => handleDelete()}
            />
          </>
        )}
        {!demo && mode === "edit" && item.id === "" && (
          <>
            <AddIcon fontSize="small" sx={addIconSx} />
          </>
        )}
      </Box>
    );
  },
  (a, b) => JSON.stringify(a) === JSON.stringify(b)
);

export default Item;

const rootSx: SxProps<Theme> = {
  border: "2px solid black",
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  flex: 1,
  position: "relative",
};

const moveIconSx: SxProps<Theme> = {
  position: "absolute",
  left: 0,
  top: 0,
  m: 0.5,
  cursor: "grab",
};

const editIconSx: SxProps<Theme> = {
  position: "absolute",
  right: 0,
  top: 0,
  m: 0.5,
  cursor: "pointer",
};

const addIconSx: SxProps<Theme> = {
  position: "absolute",
  right: 0,
  bottom: 0,
  m: 0.5,
  cursor: "pointer",
};

const deleteIconSx: SxProps<Theme> = {
  position: "absolute",
  left: 0,
  bottom: 0,
  m: 0.5,
  cursor: "pointer",
};
